
import mixin from '../libs/mixin'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

import $ from 'jquery'
export default {
  components: {
    Group,
    Cell,
    FormPreview
  },
  mixins: [mixin],
  mounted: function(){
  	this.setDocumentTitle("home")
  },
  data () {
    return {
      msg1: '这就是我的home',
      type:'收款金额',
      price:'$6600.00',
      list: [{
        label: '商品',
        value: '电动打蛋机'
      }, {
        label: '标题标题',
        value: '名字名字名字'
      }, {
        label: '标题标题',
        value: '很长很长的名字很长很长的名字很长很长的名字很长很长的名字很长很长的名字'
      }],
      buttons1: [{
        style: 'default',
        text: '辅助操作'
      }, {
        style: 'primary',
        text: '跳转到日期',
        link: '/calendar'
      }],
      buttons2: [{
        style: 'default',
        text: '我的相册',
        link: '/previewer'
      }, {
        style: 'primary',
        text: '挑选色系',
        link: '/colorpicker'
      }]
    }
  }
}



